/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul {
  margin: 0;
  padding: 0;
}



/**
 * Basic styling
 */
body {
  font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
  color: $text-color;
  background-color: $background-color;
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: "kern" 1;
     -moz-font-feature-settings: "kern" 1;
       -o-font-feature-settings: "kern" 1;
          font-feature-settings: "kern" 1;
  font-kerning: normal;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

/* Callout & Alert styles */
.bs-callout {
    padding: 20px;
    margin: 20px 0;
    border: 1px solid #eee;
    border-left-width: 5px;
    border-radius: 3px;
}
.bs-callout h4 {
    margin-top: 0;
    margin-bottom: 5px;
}
.bs-callout p:last-child {
    margin-bottom: 0;
}
.bs-callout code {
    border-radius: 3px;
}
.bs-callout+.bs-callout {
    margin-top: -5px;
}
.bs-callout-default {
    border-left-color: #777;
}
.bs-callout-default h4 {
    color: #777;
}
.bs-callout-primary {
    border-left-color: #428bca;
}
.bs-callout-primary h4 {
    color: #428bca;
}
.bs-callout-success {
    border-left-color: #5cb85c;
}
.bs-callout-success h4 {
    color: #5cb85c;
}
.bs-callout-danger {
    border-left-color: #d9534f;
}
.bs-callout-danger h4 {
    color: #d9534f;
}
.bs-callout-warning {
    border-left-color: #f0ad4e;
}
.bs-callout-warning h4 {
    color: #f0ad4e;
}
.bs-callout-info {
    border-left-color: #5bc0de;
}
.bs-callout-info h4 {
    color: #5bc0de;
}
.bs-callout-ut {
    border-left-color: #FF8200;
}
.bs-callout-ut h4 {
    color: #FF8200;
}


.alert {
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid transparent;
    border-radius: 4px
}

.alert h4 {
    margin-top: 0;
    color: inherit
}

.alert .alert-link {
    font-weight: 700
}

.alert>p,
.alert>ul {
    margin-bottom: 0
}

.alert>p+p {
    margin-top: 5px
}

.alert-dismissable,
.alert-dismissible {
    padding-right: 35px
}

.alert-dismissable .close,
.alert-dismissible .close {
    position: relative;
    top: -2px;
    right: -21px;
    color: inherit
}

.alert-success {
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6
}

.alert-success hr {
    border-top-color: #c9e2b3
}

.alert-success .alert-link {
    color: #2b542c
}

.alert-info {
    color: #31708f;
    background-color: #d9edf7;
    border-color: #bce8f1
}

.alert-info hr {
    border-top-color: #a6e1ec
}

.alert-info .alert-link {
    color: #245269
}

.alert-warning {
    color: #8a6d3b;
    background-color: #fcf8e3;
    border-color: #faebcc
}

.alert-warning hr {
    border-top-color: #f7e1b5
}

.alert-warning .alert-link {
    color: #66512c
}

.alert-danger {
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1
}

.alert-danger hr {
    border-top-color: #e4b9c0
}

.alert-danger .alert-link {
    color: #843534
}

.bs-callout > ul {
    margin-bottom: 0
}

/* End Callout & Alert styles*/

dl {
    display: flex;
    flex-flow: row wrap;
    border: hidden;
}

dt {
    flex-basis: 40%;
    padding: 2px 4px;
    text-align: left;
    font-weight: bold;
}

dd {
    flex-basis: 50%;
    flex-grow: 1;
    margin: 0;
    padding: 2px 4px;
}

/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl,
%vertical-rhythm {
  margin-bottom: calc($spacing-unit / 2);
}

h1 {
  margin-top: calc($spacing-unit * 1.65);
}

h2 {
  margin-top: calc($spacing-unit * 1.45);
}

h3 {
  margin-top: calc($spacing-unit * 1.25);
  color: #CC8604;
}

h3 + p.epigraph {
  padding-top: 0.5em;
  margin-top: -1em;
}

h4 {
  margin-top: $spacing-unit;
}

h5 {
  margin-top: calc($spacing-unit / 1.2);
  color: #CC04AE;
}

h6 {
  margin-top: calc($spacing-unit / 1.5);
  font-style: italic;
}

kbd {
  border-radius: 3px;
  padding: 1px 2px 0;
  border: 1px solid black;
}

/**
 * `main` element
 */
main {
  display: block; /* Default value of `display` of `main` element is 'inline' in IE 11. */
}

.poem_offset {
  margin-right: 30px;
  margin-left: 120px;
  max-width: 35em;
  }

.pubTitle {
  font-weight: bold;
}

/**
 * Images
 */
img {
  max-width: 100%;
  vertical-align: middle;
}



/**
 * Figures
 */

figure {
  margin: 1em 60px
}

figure > img {
  display: block;
}

figcaption {
  font-size: $small-font-size;
}

.post-content img {
    margin: 12px 0px 3px 0px;
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}

/**
 * Lists
 */
ul, ol {
  margin-left: $spacing-unit;
}

ol > li {
  margin-bottom: calc($spacing-unit / 4);
}

li {
  > ul,
  > ol {
    margin-bottom: 0;
  }
}

// li > ol, ul {
//   margin-bottom: $spacing-unit;
// }

p + ol,
p + ul {
  padding-top: 0px;
  margin-top: -10px;
  }

/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
  font-weight: $base-font-weight;
}



/**
 * Links
 */
a {
  color: $brand-color;
  text-decoration: none;

  &:visited {
    color: darken($brand-color, 15%);
  }

  &:hover {
    color: $text-color;
    text-decoration: underline;
  }

  .social-media-list &:hover {
    text-decoration: none;

    .username {
      text-decoration: underline;
    }
  }
}


/**
 * Blockquotes
 */
blockquote {
  color: $grey-color;
  border-left: 4px solid $grey-color-light;
  padding-left: calc($spacing-unit / 2);
  @include relative-font-size(1.125);
  letter-spacing: -1px;
  font-style: italic;

  > :last-child {
    margin-bottom: 0;
  }
}



/**
 * Code formatting
 */
pre,
code {
  @include relative-font-size(0.9375);
  border: 1px solid $grey-color-light;
  border-radius: 3px;
  background-color: #eef;
}

code {
  padding: 1px 5px;
}

pre {
  padding: 8px 12px;
  overflow-x: auto;

  > code {
    border: 0;
    padding-right: 0;
    padding-left: 0;
  }
}



/**
 * Wrapper
 */
.wrapper {
  max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
  max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
  margin-right: auto;
  margin-left: auto;
  padding-right: $spacing-unit;
  padding-left: $spacing-unit;
  @extend %clearfix;

  @include media-query($on-laptop) {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
    max-width:         calc(#{$content-width} - (#{$spacing-unit}));
    padding-right: calc($spacing-unit / 2);
    padding-left: calc($spacing-unit / 2);
  }
}



/**
 * Clearfix
 */
%clearfix:after {
  content: "";
  display: table;
  clear: both;
}



/**
 * Icons
 */

.svg-icon {
    width: 16px;
    height: 16px;
    display: inline-block;
    fill: #{$grey-color};
    padding-right: 5px;
    vertical-align: text-top;
}

.social-media-list {
  li + li {
    padding-top: 5px;
  }
}



/**
 * Tables
 */
table {
  margin-bottom: $spacing-unit;
  width: 100%;
  text-align: $table-text-align;
  color: lighten($text-color, 18%);
  border-collapse: separate;
  border: 0px;
  /*tr {
    &:nth-child(even) {
      background-color: lighten($grey-color-light, 6%);
    }
  }*/
  th, td {
    padding: (calc($spacing-unit / 3)) (calc($spacing-unit / 2));
  }
  th {
    background-color: lighten($grey-color-light, 3%);
    border: 0px;
    border-bottom-color: darken($grey-color-light, 12%);
  }
  td {
    border: 0px;
  }
}

/* from David Elbe: https://david.elbe.me/jekyll/2015/06/20/how-to-link-to-next-and-previous-post-with-jekyll.html */
.PageNavigation {
  font-size: 14px;
  display: block;
  width: auto;
  overflow: hidden;
}

.PageNavigation a {
  display: block;
  width: 50%;
  float: left;
  margin: 1em 0;
}

.PageNavigation .next {
  text-align: right;
  float: right;
}

/* LABELS */
.label {
  display: inline;
  padding: .2em .6em .3em;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25em
  }

.label a {
  color: #fdfdfd;
  font: $base-font-family;
  font-size: $base-font-size * 1.15;
  text-decoration: none;
}

  a.label:focus,
  a.label:hover {
      color: #fff;
      text-decoration: none;
      cursor: pointer
  }

  .label:empty {
      display: none
  }

  .btn .label {
      position: relative;
      top: -1px
  }

  .label-default {
      background-color: #777
  }

  .label-default[href]:focus,
  .label-default[href]:hover {
      background-color: #5e5e5e
  }

  .label-primary {
      background-color: #337ab7
  }

  .label-primary[href]:focus,
  .label-primary[href]:hover {
      background-color: #286090
  }

  .label-success {
      background-color: #5cb85c
  }

  .label-success[href]:focus,
  .label-success[href]:hover {
      background-color: #449d44
  }

  .label-info {
      background-color: #5bc0de
  }

  .label-info[href]:focus,
  .label-info[href]:hover {
      background-color: #31b0d5
  }

  .label-warning {
      background-color: #f0ad4e
  }

  .label-warning[href]:focus,
  .label-warning[href]:hover {
      background-color: #ec971f
  }

  .label-danger {
      background-color: #d9534f
  }

  .label-danger[href]:focus,
  .label-danger[href]:hover {
      background-color: #c9302c
  }
/* END LABELS */

/*  POEM STYLES */
.right {
  text-align: right;
}

.noStyle {
  font-style: normal;
}

#title {
  text-transform: capitalize;
}

span + h3,
dt h3 {
  padding-top: 0px;
  margin-top: -5px;
}

/* dt + h3 {
  padding-top: 0px;
  margin-top: -5px;
} */

q {
  font-style: italic;
  quotes: none;
}


.dedication {
  font-style: italic;
  padding-left: 15em;
  padding-top: 0.5em;
}

.epigraph {
  font-style: italic;
  padding-left: 4em;
  padding-bottom: 1em;
}

cite.album,
cite.book,
cite.game,
cite.journal,
cite.magazine,
cite.movie,
cite.podcast,
cite.series,
cite.tv,
cite.vgame  {
  font-style: italic;
  quotes: none;
  }

cite.article,
cite.poem,
cite.song {
  font-style: normal;
  quotes: auto;
  }

cite.article::before,
cite.poem::before,
cite.song::before {
  content: open-quote;
  }

cite.article::after,
cite.poem::after,
cite.song::after {
  content: close-quote;
  }

.center {
  text-align: center;
}

.hangAgain {
  padding-left: 8em;
  text-indent: -4em;
  } /*Duende 1*/

.hangAgainAgain {
  padding-left: 12em;
  text-indent: -8em;
  } /* Curse */

.hangFirst {
  padding-left: 4em;
  text-indent: 4em;
  }

.hangFirstDouble {
  padding-left: 6em;
  text-indent: 6em;
  } /*NtS Present*/

.hangFirstHalf {
  padding-left: 1.5em;
  text-indent: 1.5em;
  } /*Low Moon*/

.hangFirst34 {
  padding-left: 3em;
  text-indent: 3em;
  } /*Black is the Color*/

.hanging {
  padding-left: 4em;
  text-indent: -4em;
  } /*Common long line | Also useful for creating tabbed non-breaking space on a singlelineâ€”use span="hanging" without a break*/

.headRoom {
  margin-top: 2em;
  }

.heavyHang {
  padding-left: 17em;
  text-indent: -17em;
  } /*MadBear line*/

.note {
  font-family: Calibri, Garamond, serif;
  font-size: 9px;
  font-weight: 100;
  }

.page-break {
  break-before: always;
  page-break-before: always;
  }

/* Allows two alignments on same line */
.rightFloat {
  float: right;
} /* American Summer 2014 */

.title {
  font-style: italic;
}

